import React from "react"
import Img from "gatsby-image"
import styled from "styled-components"
import SEO from "../components/seo"
import SectionContainer from "../components/SectionContainer"

//const Container = styled.div`
//  display: grid;
//  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
//  grid-grap: 0.5rem;
//  align-items: right;
//`

const Container = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.5rem;
  .gatsby-image-wrapper {
    height: 200px;
    filter: grayscale(1);
  }
`

const StyledDiv = styled.div`
  font-size: 24px;
  padding: 12px;
  text-align: center;
  margin-bottom: 1rem;
  position: relative;
  z-index: 2;
  color: black;
`

function IndexPage({ data }) {
  const nodes = data.sections.nodes
  return (
    <>
      <SEO title="Home" />
      <h1></h1>
      <p>Learn about the historical Monte Rosa / Neptune Lodge.</p>
      <Container>
        {nodes.map(v => {
          return (
            <SectionContainer link={`section/${v.slug.current}`}>
              <Img fluid={v.images[0].asset.fluid} alt={v.name} />
              <StyledDiv key={v.id}>{v.name}</StyledDiv>
            </SectionContainer>
          )
        })}
      </Container>
    </>
  )
}

export default IndexPage

export const pageQuery = graphql`
  query SectionQuery {
    sections: allSanitySection(sort: { fields: name }) {
      nodes {
        slug {
          current
        }
        id
        name
        images {
          asset {
            fluid(maxWidth: 400) {
              ...GatsbySanityImageFluid
            }
          }
        }
      }
    }
  }
`
